import { Injectable } from '@angular/core';
import { LoggerService } from '../core/logger.service';

@Injectable()
export class TempService {

  constructor(private logger: LoggerService) {
    logger.log('temp service constructor');
  }

  tempwrite(msg: string) {
    console.log(msg);
  }

}



